.button-scroll--right, .button-scroll--left, .button-scroll {
  width: 48px;
  height: 48px;
  border-radius: 50%;
  background: #fff;
  border: solid 1px #408F95;
  position: relative;
  cursor: pointer;
}
@media screen and (min-width: 1177px) {
  .button-scroll--right, .button-scroll--left, .button-scroll {
    width: 80px;
    height: 80px;
  }
}

.button-scroll--right::before, .button-scroll--left::before, .button-scroll::before {
  position: absolute;
  content: "";
  display: block;
  width: 10px;
  height: 1px;
  top: 50%;
  background-color: #408F95;
}

.button-scroll--right::after, .button-scroll--left::after, .button-scroll::after {
  position: absolute;
  content: "";
  display: block;
  width: 28px;
  height: 1px;
  top: 50%;
  left: calc((100% - 28px) / 2);
  background-color: #408F95;
}
@media screen and (min-width: 1177px) {
  .button-scroll--right::after, .button-scroll--left::after, .button-scroll::after {
    width: 2.5rem;
    left: calc((100% - 40px) / 2);
  }
}

.button-scroll {
  transform: rotate(90deg);
}

.button-scroll::before {
  top: calc(50% - 3px);
  left: calc((100% - 28px) / 2);
  transform: rotate(-30deg);
}
@media screen and (min-width: 1177px) {
  .button-scroll::before {
    left: calc((100% - 40px) / 2);
  }
}

.button-scroll--left::before {
  top: calc(50% - 3px);
  right: calc((100% - 28px) / 2);
  transform: rotate(30deg);
}
@media screen and (min-width: 1177px) {
  .button-scroll--left::before {
    right: calc((100% - 40px) / 2);
  }
}

@media screen and (min-width: 1177px) {
  .swiper-campaign-button-next .button-scroll--left::after {
    left: calc((100% - 40px) / 2);
  }
}

.button-scroll--right::before {
  top: calc(50% - 3px);
  left: calc((100% - 28px) / 2);
  transform: rotate(-30deg);
}
@media screen and (min-width: 1177px) {
  .button-scroll--right::before {
    left: calc((100% - 40px) / 2);
  }
}

.button {
  position: relative;
  z-index: 1;
  background-color: #408F95;
  border: 1px solid #408F95;
  color: #fff;
  transition: 0.2s cubic-bezier(0.45, 0, 0.55, 1);
  display: inline-block;
  font-family: "Gotu", sans-serif;
  font-size: 0.875rem;
  font-weight: 400;
  line-height: 1;
  padding: 1.1875rem 5.4375rem 1.25rem 2.4375rem;
  cursor: pointer;
  margin: 0 0 0.375rem 0;
}

.button::before {
  position: absolute;
  width: 100%;
  height: 100%;
  content: "";
  top: 0.25rem;
  left: 0.25rem;
  border: 1px solid #408F95;
  z-index: -1;
}

.button::after {
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  width: 100%;
  height: 100%;
  background: #fff;
  border: 1px solid #fff;
  transform: scale(0, 1);
  transform-origin: left top;
  transition: 0.2s cubic-bezier(0.45, 0, 0.55, 1);
  z-index: -1;
}

.button:hover {
  color: #408F95;
  background-color: #fff;
}

.button:hover::after {
  transform: scale(1, 1);
}

.button__text {
  position: relative;
  z-index: 2;
}

.button__text::before {
  position: absolute;
  content: "";
  width: 0.75rem;
  height: 0.0625rem;
  top: calc(50% - 0.1875rem);
  right: -2.96875rem;
  transform: rotate(30deg);
  background-color: #fff;
  z-index: 2;
}

.button:hover .button__text::before,
.button:hover .button__text::after {
  background-color: #408F95;
  z-index: 2;
}

.button__text::after {
  position: absolute;
  content: "";
  width: 2.5rem;
  height: 0.0625rem;
  top: calc(50% + 0.0625rem);
  right: -3rem;
  background: #fff;
}

